import {
	Box,
	Button,
	Divider,
	Drawer,
	DrawerBody,
	DrawerContent,
	DrawerFooter,
	DrawerHeader,
	DrawerOverlay,
	Flex,
	Image,
	useDisclosure,
	VStack,
} from "@chakra-ui/react";
import { TfiMenu } from "react-icons/tfi";
import React from "react";
import { Link } from "react-router-dom";
import logo from "../Data/images/name.png";

const Navbar = () => {
	const { isOpen, onOpen, onClose } = useDisclosure();
	const btnRef = React.useRef();
	return (
		<Flex m={2} p={4} pl={[4, 8]} justifyContent="space-between" mb={0}>
			<Button ref={btnRef} size={"2xl"} variant="unstyled" onClick={onOpen}>
				<TfiMenu w={10} />
			</Button>
			<Drawer
				isOpen={isOpen}
				placement="left"
				onClose={onClose}
				finalFocusRef={btnRef}
			>
				<DrawerOverlay />
				<DrawerContent bgColor="transparent">
					<DrawerHeader
						bgColor={"#FFDE59"}
						borderBottomRightRadius={"25"}
						borderBottomWidth="1px"
						fontFamily={"heading"}
						fontWeight="bold"
					>
						Menu
					</DrawerHeader>

					<DrawerBody bgColor={"whitesmoke"} w={"93%"}>
						<VStack pl={4} pt={4} w={"75%"} gap={5} alignItems="left">
							<Box
								borderBottomWidth={2}
								borderBottomLeftRadius={"25"}
								pl={"4"}
								borderColor="#FFDE59"
							>
								<Link>
									<Button
										size="lg"
										fontFamily={"heading"}
										pl={"4"}
										color="black"
										variant={"unstyled"}
									>
										About Me
									</Button>
									<Divider w={"75%"} colorScheme />
								</Link>
							</Box>

							<Box
								borderBottomWidth={2}
								borderBottomLeftRadius={"25"}
								pl={"4"}
								borderColor="#FFDE59"
							>
								<Link>
									<Button
										size="lg"
										fontFamily={"heading"}
										pl={"4"}
										color="black"
										variant={"unstyled"}
									>
										Skills
									</Button>
								</Link>
							</Box>
							<Box
								borderBottomWidth={2}
								borderBottomLeftRadius={"25"}
								pl={"4"}
								borderColor="#FFDE59"
							>
								<Link>
									<Button
										size="lg"
										fontFamily={"heading"}
										pl={"4"}
										color="black"
										variant={"unstyled"}
									>
										Experience
									</Button>
								</Link>
							</Box>
							<Box
								borderBottomWidth={2}
								borderBottomLeftRadius={"25"}
								pl={"4"}
								borderColor="#FFDE59"
							>
								<Link>
									<Button
										size="lg"
										fontFamily={"heading"}
										pl={"4"}
										color="black"
										variant={"unstyled"}
									>
										Projects
									</Button>
								</Link>
							</Box>
							<Box
								borderBottomWidth={2}
								borderBottomLeftRadius={"25"}
								pl={"4"}
								borderColor="#FFDE59"
							>
								<Link>
									<Button
										size="lg"
										fontFamily={"heading"}
										pl={"4"}
										color="black"
										variant={"unstyled"}
									>
										Testimonials
									</Button>
								</Link>{" "}
							</Box>
							<Box
								borderBottomWidth={2}
								borderBottomLeftRadius={"25"}
								pl={"4"}
								borderColor="#FFDE59"
							>
								<Link>
									<Button
										fontFamily={"heading"}
										size="lg"
										pl={"4"}
										color="black"
										variant={"unstyled"}
									>
										Contact
									</Button>
								</Link>{" "}
							</Box>
						</VStack>
					</DrawerBody>
					<DrawerFooter
						bgColor={"#FFDE59"}
						borderTopWidth="1px"
						w={"93%"}
						borderBottomRightRadius={"25"}
						fontFamily={"heading"}
					>
						~ pratikderepatil
					</DrawerFooter>
				</DrawerContent>
			</Drawer>
			<Image src={logo} w={["50%", "50%", "30%", "20%"]} />
		</Flex>
	);
};

export default Navbar;
